@font-face {
  font-family: BPdotsSquareVF;
  src: url("BPdotsSquareVF.25fec6ee.ttf") format("truetype");
  font-display: swap;
}

@font-face {
  font-family: FunnelDisplay;
  src: url("FunnelDisplay.db902d50.woff2");
}

@font-face {
  font-family: ZxGamut;
  src: url("ZxGamut-Variable.6528b92f.woff2");
  font-display: swap;
}

html, body {
  background-color: #171717;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

a {
  text-align: center;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: BPdotsSquareVF;
  font-size: 2.5vmin;
  font-weight: 900;
  display: block;
  position: relative;
}

a:link {
  color: #4a90e2;
}

a:visited {
  color: #f5be07;
}

.main-paragraph a {
  width: auto;
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  text-align: inherit;
  display: inline;
  position: static;
}

.main-paragraph a:link {
  color: #4a90e2;
}

.main-paragraph a:visited {
  color: #f5be07;
}

nav {
  z-index: 9999;
  background: none;
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
  position: fixed;
}

.nav-mobile {
  bottom: 5vmin;
}

.vertical-center {
  margin: auto;
  padding: auto;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.horizontal-center {
  margin: auto;
  padding: auto;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.app {
  opacity: 0;
  transition: opacity .2s cubic-bezier(0, .55, .45, 1);
}

.route-mobile {
  font-size: 5vmin;
}

.route-active {
  color: #faa000;
  transition: color .3s, text-shadow .8s, box-shadow .8s;
}

.route-active:hover {
  color: #faa000;
  text-shadow: 0 0 16px #faa000;
}

.main-paragraph {
  color: #fafafa;
  text-align: left;
  width: 100%;
  margin: .5vmin 0 0;
  padding: auto;
  font-family: ZxGamut;
  font-size: 1.8vmin;
  font-weight: 400;
  position: relative;
}

.text-center {
  text-align: center;
}

h1 {
  color: #fafafa;
  text-align: left;
  width: 100%;
  margin: 0;
  padding: auto;
  font-family: BPdotsSquareVF;
  font-size: 4vmin;
  font-weight: 800;
  position: relative;
}

h2 {
  color: #fafafa;
  text-align: left;
  width: 100%;
  margin: 1.5vmin 0 .5vmin;
  padding: 1vmin 0;
  font-family: BPdotsSquareVF;
  font-size: 3vmin;
  font-weight: 800;
  position: relative;
}

h3 {
  color: #fafafa;
  text-align: left;
  margin: .5vmin 0;
  padding: auto;
  font-family: BPdotsSquareVF;
  font-size: 2.5vmin;
  font-weight: 900;
  position: relative;
}

.about-container {
  width: 100%;
  height: 100vh;
  display: flex;
  overflow: hidden;
}

.about-bio {
  scrollbar-width: none;
  -ms-overflow-style: none;
  flex: 1;
  padding: 3% 0% 3% 3%;
  overflow-y: auto;
}

.about-bio::-webkit-scrollbar {
  display: none;
}

.about-cv {
  scrollbar-width: none;
  -ms-overflow-style: none;
  flex: 1.5;
  padding: 3% 3% 3% 5%;
  overflow-y: auto;
}

.about-cv::-webkit-scrollbar {
  display: none;
}

.cv-section {
  margin-bottom: 2vmin;
}

.breadcrumb-nav {
  z-index: 9998;
  color: #fafafa;
  backdrop-filter: blur(4px);
  pointer-events: auto;
  user-select: none;
  background: #17171799;
  border: 1px solid #404040;
  align-items: center;
  padding: 10px 14px;
  font-family: BPdotsSquareVF;
  font-size: 2.4vmin;
  font-weight: 600;
  display: flex;
  position: fixed;
  bottom: 20px;
  left: 20px;
}

.breadcrumb-text {
  color: inherit;
  font-family: inherit;
}

.breadcrumb-segment {
  color: #b0b0b0;
  text-decoration: none;
  transition: color .2s;
}

.breadcrumb-segment:hover {
  color: #4a90e2;
  cursor: pointer;
}

.breadcrumb-separator {
  color: gray;
  pointer-events: none;
}

@media (width <= 1024px) {
  html, body {
    height: auto;
    overflow: auto;
  }

  .breadcrumb-nav {
    padding: 8px;
    font-size: 4vmin;
    bottom: 8px;
    left: 8px;
  }

  .about-container {
    flex-direction: column;
    height: auto;
    overflow: visible;
  }

  .about-bio, .about-cv {
    flex: none;
    padding: 5%;
    overflow: visible;
  }

  h1 {
    width: 100%;
    margin: 0;
    font-size: 5vmin;
  }

  h2 {
    width: 100%;
    margin: 1vmin 0 0;
    font-size: 3.6vmin;
  }

  h3 {
    font-size: 3vmin;
  }

  .main-paragraph {
    width: 100%;
    margin: 2vmin 0 0;
    font-size: 11px;
  }
}
